import { gql } from 'apollo-boost';

export const READ_MYDIARY = gql`
  query readMyDiary {
    readMyDiary {
      id
      content
      good
      score
      question {
        id
        type
        behavior
      }
    }
  }
`;

export const UPDATE_MYDIARY = gql`
  mutation updateMyDiary(
    $id: String
    $content: String
    $good: Boolean
    $score: Int
  ) {
    updateMyDiary(id: $id, content: $content, good: $good, score: $score)
  }
`;
