import React from 'react';
import MePresenter from './MePresenter';
import { READ_MYDIARY, UPDATE_MYDIARY } from './MeQueries';
import { useQuery, useMutation } from '@apollo/react-hooks';
import Loader from 'Components/Loader';

export default () => {
  const { data: myDiaryData, loading: myDiaryLoading } = useQuery(READ_MYDIARY);

  const [updateMyDiary] = useMutation(UPDATE_MYDIARY, {
    refetchQueries: () => [{ query: READ_MYDIARY }],
  });

  const onKeyPress = async (event, id, type, value) => {
    if (event.key === 'Enter') {
      await updateMyDiary({
        variables: { id: id, [type]: value },
      }).catch(e => console.log(e));
    }
  };

  return (
    <>
      {myDiaryLoading && <Loader />}
      {!myDiaryLoading && myDiaryData && (
        <MePresenter
          myDiaries={myDiaryData.readMyDiary}
          onKeyPress={onKeyPress}
        />
      )}
    </>
  );
};
