import React from 'react';
import styled from 'styled-components';
import { gql } from 'apollo-boost';
import { useQuery } from '@apollo/react-hooks';
import Loader from '../Components/Loader';
import Post from '../Routes/Post';
import Tab from '../Components/Tab';
import Write from '../Components/Write';
import Header from '../Components/Header';
import { useHistory, useLocation } from 'react-router-dom';
import { READ_MYDATA } from '../sharedQueries';
import Timeline from 'Components/Timeline';

const FEED_QUERY = gql`
  {
    seeFeed {
      id
      title
      caption
      user {
        id
        avatar
        userName
      }
      files {
        id
        url
      }
      likeCount
      isLiked
      comments {
        id
        text
        user {
          id
          userName
        }
      }
      createdAt
    }
  }
`;

const DIARY_QUERY = gql`
  {
    readAllDiary {
      id
      content
      good
      score
      user {
        id
      }
      question {
        id
        type
        behavior
      }
      createdAt
    }
  }
`;

const Wrapper = styled.div`
  display: grid;
  grid-template-rows: 7vh 7vh 8vh;
`;

const PostList = styled.div`
  display: grid;
  justify-content: center;
  grid-gap: 1px;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  text-align: center;
`;

const TimelineWrapper = styled.div`
  display: grid;
  height: 100%;
  background: white;
  padding: 10vw;
  padding-top: 0;
  padding-bottom: 0;
`;
export default () => {
  console.log('feed');
  let location = useLocation();
  let history = useHistory();
  if (location.pathname === '/auth') {
    history.push('/theme');
  }
  const { data: readMyData, loading: myDataLoading } = useQuery(READ_MYDATA);
  const { data: postsData, loading: postsLoading } = useQuery(FEED_QUERY, {
    pollInterval: 5000,
  });
  const { data: diariesData, loading: diariesLoading } = useQuery(DIARY_QUERY, {
    pollInterval: 5000,
  });
  if (postsLoading || myDataLoading || diariesLoading) {
    return <Loader />;
  }

  const posts = postsData.seeFeed;
  const diaries = diariesData.readAllDiary;
  const diariesAndPosts = [...posts, ...diaries].sort((posts, diaries) =>
    posts.createdAt > diaries.createdAt ? -1 : 1
  );

  return (
    <>
      {readMyData &&
        readMyData.readMyData &&
        (readMyData.readMyData.avatar === 'avatar.png'
          ? history.push('/theme')
          : '')}
      <Wrapper>
        <Header />
        <Tab />
        <Write />
        <PostList>
          {!(postsLoading || diariesLoading) &&
            postsData &&
            diariesData &&
            postsData.seeFeed &&
            diariesData.readAllDiary &&
            diariesAndPosts.map(feed =>
              feed.__typename === 'Post' ? (
                <Post
                  key={feed.id}
                  id={feed.id}
                  title={feed.title}
                  caption={feed.caption}
                  user={feed.user}
                  files={feed.files}
                  likeCount={feed.likeCount}
                  isLiked={feed.isLiked}
                  comments={feed.comments}
                  createdAt={feed.createdAt}
                />
              ) : (
                <TimelineWrapper key={feed.id}>
                  <Timeline diariesArr={[feed]} />
                </TimelineWrapper>
              )
            )}
        </PostList>
      </Wrapper>
    </>
  );
};
