import React from 'react';
import styled from 'styled-components';

const Brand = styled.div`
  display: grid;
  cursor: pointer;
  font-family: 'Brand';
  font-size: 8vw;
  color: ${props => props.theme.dimgreyColor};
  font-weight: 800;
  text-align: end;
`;

export default () => <Brand>DISORDER</Brand>;
