import React from 'react';
import styled from 'styled-components';
//import TextareaAutosize from 'react-autosize-textarea/lib';
import useInput from 'Hooks/useInput';
import { useHistory, useLocation } from 'react-router-dom';

const Timeline = styled.div`
  display: grid;
  grid-column: 100%;
  grid-auto-rows: 17vh;
`;
const TimelineTail = styled.div`
  position: absolute;
  height: calc(100% - 2vw);
  left: 2vw;
  top: 3vw;
  border-left: 2vw solid ${props => props.theme.lightGreyColor};
`;

const TimlineCircle = styled.div`
  position: absolute;
  width: 6vw;
  height: 6vw;
  background-color: ${props => props.theme.activeColor};
  border-radius: 100px;
  top: 0.5vw;
`;

const TimelineItem = styled.div`
  position: relative;
`;
const TimelineItemsWrapper = styled.div`
  display: grid;
  width: 100%;
  height: 100%;
  padding-left: 7vw;
  grid-template-areas:
    'type behavior behavior'
    'content content content'
    'content content content'
    'evaluation evaluation evaluation'
    'evaluation evaluation evaluation';
`;
const Type = styled.div`
  font-family: 'KakaoB';
  align-self: start;
  justify-self: start;
  font-size: 5vw;
  grid-area: type;
  padding-top: 0.5vw;
`;
const Content = styled.input`
  font-size: 5vw;
  padding-top: 1vw;
  grid-area: content;
  overflow-y: hidden;
  -webkit-line-clamp: 2;
  overflow: hidden;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  cursor: none;
  &:focus {
    outline: none !important;
    border-radius: 1vh;
    /* box-shadow: 0 0 10px ${props => props.theme.activeColor}; */
    border: 2.5px solid ${props => props.theme.activeColor};
  }
  resize: none;
  ${props => props.theme.removeInputStyle}
`;
const Behavior = styled.div`
  display: grid;
  align-self: start;
  justify-self: end;
  font-size: 4vw;
  border: 3px solid ${props => props.theme.activeColor};
  border-radius: 30px;
  padding: 1vw;
  grid-area: behavior;
`;
const Evaluation = styled.div`
  display: grid;
  grid-auto-flow: column;
  justify-content: start;
  justify-items: start;
  grid-area: evaluation;
`;
const Good = styled.input`
  font-family: 'KakaoL';
  width: 10vh;
  font-weight: 900;
  &:focus {
    outline: none !important;
    border-radius: 1vh;
    border: 2.5px solid ${props => props.theme.activeColor};
  }
  ${props => props.theme.removeInputStyle}
`;
const Score = styled.input`
  font-family: 'KakaoL';
  font-weight: 900;
  width: 100%;
  &:focus {
    outline: none !important;
    border-radius: 1vh;
    border: 2.5px solid ${props => props.theme.activeColor};
  }
  ${props => props.theme.removeInputStyle}
`;

const Empty = styled.div`
  display: grid;
`;

export default ({ diariesArr, onKeyPress }) => {
  let history = useHistory();
  let location = useLocation();
  console.log('history : ', history);
  console.log('location : ', location);
  console.log('Timeline');
  console.log(diariesArr);
  // const TimelineAction = {
  //   CREATE_DIARYSTATE: 'CREATE_DIARYSTATE',
  //     SET_ACTION: 'SET_ACTION',
  //     COUNT_SURVEY: 'COUNT_SURVEY',
  //     INIT_DIARY: 'INIT_DIARY',
  //     CREATE_MYDIARY: 'CREATE_MYDIARY',
  //     UPDATE_MYQUESTION: 'UPDATE_MYQUESTION',
  //     REFETCH_MYQUESTION: 'REFETCH_MYQUESTION',
  //     GET_QUESTIONSOFME: 'GET_QUESTIONSOFME',
  //     SET_MANY: 'SET_MANY',
  //     SET_DIARYCONTENT: 'SET_DIARYCONTENT',
  //     SET_DIARYQUESTION: 'SET_DIARYQUESTION',
  //   };
  // const reducer = (state, action) => {
  //   switch(action.type) {
  //     case TimelineAction.CREATE_DIARYSTATE:
  //       return {
  //         ...state,[action.id]:{
  //           content:a,
  //         }
  //       };
  //     default:
  //       return state;
  //   }
  // }

  // const [state, dispatch]
  //  = useReducer(reducer,{});

  // const content = useInput();
  // const good = useInput();
  // const score = useInput();

  return (
    <Timeline>
      {diariesArr ? (
        diariesArr.map(diary => {
          const content = useInput();
          const good = useInput();
          const score = useInput();
          return (
            <TimelineItem key={diary.id}>
              <TimelineTail />
              <TimlineCircle />
              <TimelineItemsWrapper>
                <Type>{diary.question.type}</Type>
                <Behavior>{diary.question.behavior ? '행동' : '생각'}</Behavior>
                {location.pathname === '/me' ? (
                  <>
                    <Content
                      diarytype={'content'}
                      diaryid={diary.id}
                      defaultValue={diary.content}
                      value={content.value}
                      onChange={content.onChange}
                      onKeyPress={event =>
                        onKeyPress(event, diary.id, 'content', content.value)
                      }
                      // onBlur={event =>
                      //   onKeyPress(event, diary.id, 'content', content.value)
                      // }
                    />
                    <Evaluation>
                      <Good
                        diarytype={'good'}
                        diaryid={diary.id}
                        defaultValue={`${diary.good ? '긍정적' : '부정적'}느낌`}
                        value={good.value}
                        onChange={good.onChange}
                      />

                      <Score
                        diarytype={'score'}
                        diaryid={diary.id}
                        // style={
                        //   diary.good ? { color: '#00695C' } : { color: '#FF5722' }
                        // }
                        defaultValue={diary.score}
                        value={score.value}
                        onChange={score.onChange}></Score>
                    </Evaluation>
                  </>
                ) : (
                  <>
                    <Content defaultValue={diary.content} disabled />
                    <Evaluation>
                      <Good
                        defaultValue={`${diary.good ? '긍정적' : '부정적'}느낌`}
                        disabled
                      />

                      <Score defaultValue={diary.score} disabled />
                    </Evaluation>
                  </>
                )}
              </TimelineItemsWrapper>
            </TimelineItem>
          );
        })
      ) : (
        <Empty />
      )}
    </Timeline>
  );
};
